<template>
    <div class="my-pay">
        <cf-widget
            v-if="showSummary"
            :data="summary"
            :loading="summaryLoading"
            class="paysummary w-100"
            icon="icon-credit-card"
            title="Pay Summary">
        </cf-widget>
        <tile :nopadding="true" class="w-100 my-payslips-widget" icon="icon-page" title="My Payslips">
            <div class="section">
                <div class="section-table nomargin">
                    <div class="date-range-filter">
                        <span>Show payslips from</span>
                        <div class="date-picker-box default-form">
                            <div class="form-row inline">
                                <datepicker v-model="dateFrom" @change="refresh()"></datepicker>
                            </div>
                        </div>
                        <span>to</span>
                        <div class="date-picker-box default-form">
                            <div class="form-row inline">
                                <datepicker v-model="dateTo" @change="refresh()"></datepicker>
                            </div>
                        </div>
                    </div>
                    <div v-if="loading" class="spinnerbox large grid-loader">
                        <img :src="payslipLoader"/>
                    </div>
                    <grid v-else :columns="columns" :data="data" :no-data-text="emptyText">
                        <div slot="buttons">
                            <a class="button blue w-icon" href>
                                <span class="icon-pencil"></span>
                                Edit
                            </a>
                        </div>
                    </grid>
                </div>
            </div>
            <modal :visible="payslipModalVisible" @close="onPayslipModalClose">
                <div v-if="payslipLoading" class="spinnerbox large">
                    <img :src="payslipLoader"/>
                </div>
                <div v-else class="payslip-modal-body" v-html="payslipHtml"></div>
            </modal>
        </tile>
    </div>
</template>

<script>
import Vue from "vue";
import moment from "moment";
import {mapActions} from "vuex";
import CfWidget from "../components/cf-widget";

export default {
    props: {
        configHost: String
    },
    data() {
        return {
            data: null,
            summary: [],
            showSummary: false,
            summaryLoading: true,
            loading: true,
            payslip: null,
            payslipHtml: null,
            payslipLoading: false,
            payslipModalVisible: false,
            payslipLoader: document.config.loaders.large.light,
            columns: [
                {
                    name: "PeriodEndDate",
                    header: "Period",
                    type: "date",
                    format(value, row) {
                        return (
                            moment(value).format("DD/MM/YYYY") + " " + row.PayTypeDescription
                        );
                    }
                },
                {
                    header: "",
                    buttons: [
                        {
                            color: "blue",
                            icon: "icon-save",
                            text: "Download",
                            href(row) {
                                return (
                                    "/Payslips/DownloadPDF?PayPoint=" +
                                    row.PayPoint +
                                    "&EndDate=" +
                                    moment(row.PeriodEndDate).format("YYYY-MM-DD") +
                                    "&PayType=" +
                                    row.PayType +
                                    "&PaidDate=" +
                                    moment(row.PaymentDate).format("YYYY-MM-DD")
                                );
                            }
                        },
                        {
                            color: "blue",
                            icon: "icon-page",
                            text: "View",
                            click: row => {
                                this.payslipHtml = null;
                                this.payslipLoading = true;
                                this.payslipModalVisible = true;
                                this.payslip = row;

                                this.$ajax
                                    .post(
                                        "/Payslips/GetPayslipHTML",
                                        {
                                            PayPoint: this.payslip.PayPoint,
                                            EndDate: moment(row.PeriodEndDate).format("YYYY-MM-DD"),
                                            PayType: row.PayType,
                                            PaidDate:  moment(row.PaymentDate).format("YYYY-MM-DD")
                                        },
                                        {
                                            responseType: "text"
                                        }
                                    )
                                    .then(resp => {
                                        let html =
                                            resp && typeof resp.data == "string" ? resp.data : null;
                                        if (!html) return this.logRequestError(resp);

                                        this.payslipHtml = html;
                                        this.payslipLoading = false;
                                    });
                            }
                        }
                    ]
                }
            ],
            dateFrom: null,
            dateTo: null
        };
    },
    methods: {
        ...mapActions(["logRequestError", "showRequestError"]),
        refresh() {
            this.loading = true;

            this.$ajax
                .post("/api/Payslips/GetPayslips", {
                    fromDate: moment(this.dateFrom).format("YYYY-MM-DD"),
                    toDate: moment(this.dateTo).format("YYYY-MM-DD")
                })
                .then(resp => {
                    this.data = resp.data;
                })
                .catch(resp => {
                    this.showRequestError({
                        resp,
                        title: "Couldn't retrieve payslips"
                    });
                })
                .then(() => {
                    this.loading = false;
                });
        },
        loadSummary() {
            this.showSummary = true;

            this.$ajax
                .post("/api/Payslips/GetPaySummary")
                .then(resp => {
                    resp.data.forEach(f => {
                        if (f.Key === "TaxCode") {
                            f.Value = `[${f.KeyValue}] ${f.Value}`;
                        } else if (
                            f.Value &&
                            (f.InputType === "Currency" || f.Key === "AnnualSalary")
                        ) {
                            f.Value = "$" + f.Value;
                        }
                    });
                    this.summary = resp.data;
                })
                .catch()
                .then(() => {
                    this.summaryLoading = false;
                });
        },
        onPayslipModalClose() {
            this.payslipModalVisible = false;
        }
    },
    computed: {
        emptyText() {
            return `There are no payslips between ${moment(this.dateFrom).format(
                "LL"
            )} and ${moment(this.dateTo).format("LL")}`;
        }
    },
    created() {
        console.log('payslips created')
        this.$ajax.post("/api/Config/MyPay").then(resp => {
            if (resp.data.ShowSummary) this.loadSummary();
        });

        this.$ajax.get("/api/Payslips/GetPeriodEndDate").then(resp => {
            this.dateTo = moment(resp.data.PeriodEndDate).toDate();
            this.dateFrom = moment(this.dateTo)
                .subtract(3, "M")
                .toDate();
            this.refresh();
        });
    },
    components: {
        CfWidget
    }
};
</script>

<style lang="scss">
.my-pay {
    .paysummary {
        .item {
            width: auto;
        }

        .label {
            width: 200px;
        }
    }

    .my-payslips-widget {
        .ui-grid .col-buttons {
            width: 180px;
        }

        .grid-loader {
            text-align: center;
            padding-bottom: 10px;
        }
    }
}
</style>
